import { Component, OnInit } from '@angular/core';
import { CatalogSandbox } from '@sandboxes/catalog.sandbox';
import { IUser, UserTypeEnum } from '@store/models/user.model';
import { UserSandbox } from '@sandboxes/user.sandbox';

interface IUserReceipts {
  name: string;
  timestamp: string;
  reference: string;
}

interface IMerchantReceipts {
  name: string;
  timestamp: string;
  reference: string;
  amount: number;
}

@Component({
  selector: 'app-tab2',
  templateUrl: './tab2.page.html',
  styleUrls: ['./tab2.page.scss']
})
export class Tab2Page implements OnInit {

  readonly userReceipts: IUserReceipts[] = [
    {
      name: 'Event Ticket - All Weekend',
      timestamp: 'April 20, 2019',
      reference: 'evt42019-01234567789'
    },
    {
      name: 'Coffee',
      timestamp: 'April 23, 2019',
      reference: 'cfe42019-01234567789'
    },
    {
      name: 'Falafel and Shake',
      timestamp: 'April 23, 2019',
      reference: 'flfshk42019-01234567789'
    },
    {
      name: 'Tegridy Farms T-Shirt - Large',
      timestamp: 'April 20, 2019',
      reference: 'tftl42019-01234567789'
    }
  ];

  readonly merchantReceipts: IMerchantReceipts[] = [
    {
      name: 'Event Ticket - All Weekend',
      timestamp: 'April 20, 2019',
      reference: 'evt42019-01234567789',
      amount: 15
    },
    {
      name: 'Event Ticket - All Weekend',
      timestamp: 'April 20, 2019',
      reference: 'evt42019-11111111111',
      amount: 15
    },
    {
      name: 'Event Ticket - All Weekend',
      timestamp: 'April 20, 2019',
      reference: 'evt42019-2222222222',
      amount: 15
    },
    {
      name: 'Event Ticket - All Weekend',
      timestamp: 'April 20, 2019',
      reference: 'evt42019-33333333333',
      amount: 15
    },
  ];

  name: string;
  isMerchant: boolean;
  receipts: IUserReceipts[] | IMerchantReceipts[] = this.merchantReceipts;

  constructor(
      private userSandbox: UserSandbox,
  ) {}

  ngOnInit() {
    this.userSandbox.user$.subscribe((user: IUser) => {
      this.name = user.companyName;
      this.isMerchant = user.role === UserTypeEnum.MERCHANT;
      this.receipts = this.isMerchant ? this.merchantReceipts : this.userReceipts;
    });
  }
}
